// Assessment wrapper
.assessment-page-wrapper {


}

// Sensei Questions
//TODO: I did not wrap the styling in the wrapper - this needs to be done later.

.single-quiz {
  img {
    max-width: 100%;
    height: auto;
  }
}

.drag-and-drop-sequential {
  ._top-mobile-title {
    display: none;
  }

  ._answer-container {
    ._box-image-container {
      width: 100%;
      height: 300px;
      border: 1px solid #ccc;
    }

    ._image-container {
      ._sortable-spot {
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
      }

      svg {
        font-size: 30px;
      }

      ._arrow-down svg {
        display: none;
      }

      ._arrow-right {
        position: absolute;
        top: 50%;
        left: -29px;
        transform: translateY(-50%);
      }
    }

    &:first-child svg {
      display: none;
    }
  }

  @media (max-width: 768px) {
    ._option-image {
      display: flex;
      flex-direction: column;
      align-items: center
    }

    ._top-mobile-title {
      display: block;
    }

    ._arrow-right svg {
      display: none;
    }

    ._box-image-container {
      width: 50%;
      margin-right: 20px !important;
    }

    ._arrow-down {
      position: absolute;
      left: 50%;
      bottom: -50px;
      transform: translateX(-50%);

      svg {
        display: block !important;
      }
    }
  }

  @media (max-width: 425px) {
    ._arrow-down {
      bottom: -48px !important;
    }
  }

  @media (max-width: 400px) {
    ._box-image-container ._arrow-down {
      bottom: -34px !important;
    }
  }
}

.drag-and-drop-non-sequential {
  ._answer-container {
    ._image-container {
      ._sortable-spot {
        width: 50%;
        height: 50%;
        right: 0;
        bottom: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        border-left: 1px solid white;
        border-top: 1px solid white;
        background: rgba(#fff, 0.333);
        overflow: hidden;

        img {
          width: auto;
          height: auto;
        }
      }
    }
  }
}

.drag-and-drop-non-sequential,
.drag-and-drop-sequential {
  ._text-mobile {
    display: none;
  }

  ._answer-container {
    display: flex;
  }

  ._image-container {
    position: relative;
    text-align: center;

    ._sortable-spot {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  ._option-image {
    ._image-letters {
      display: none;
    }
  }

  .answers {
    padding: 0;

    input[type="checkbox"] {
      display: none;
    }
  }

  ._option-images {
    border-bottom: 1px solid #ccc;
    margin-bottom: 30px;
    padding-bottom: 20px;
  }

  ._mobile-images-answers {
    display: none;
  }

  @media (max-width: 768px) {
    ._text-desktop {
      display: none;
    }

    ._text-mobile {
      display: block;
    }

    ._option-image {
      ._image-letters {
        display: block !important;
      }
    }

    ._mobile-images-answers {
      display: block !important;
    }

    ._images-answers {
      flex-direction: column;
      align-items: center;
      justify-content: center;

      ._answer-container {
        padding-bottom: 60px;
        align-items: center;
        max-width: 100%;

        &:last-child ._arrow-down {
          display: none !important;
        }

        img {
          padding-right: 10px;
        }
      }
    }

    .container-fluid {
      padding-left: 0;
    }
  }

  @media (max-width: 575px) {
    ._option-image {
      width: 50%;

      &:nth-child(1),
      &:nth-child(3) {
        border-right: 1px solid #ccc;
      }

      &:nth-child(1),
      &:nth-child(2) {
        border-bottom: 1px solid #ccc;
        padding-bottom: 5px;
      }

      &:nth-child(3),
      &:nth-child(4) {
        padding-top: 10px;
      }
    }
  }

  @media (max-width: 400px) {
    ._answer-container {
      padding-bottom: 30px !important;
    }
  }
}

@media (max-width: 768px) {
  .single-quiz-wrapper .quiz-questions #sensei-quiz-list {
    .answers {
      padding: 0 !important;
    }
  }
}